/* eslint no-underscore-dangle: 0 */
import { useState, useEffect, useRef } from 'react';
import { useNavigate } from 'react-router-dom';
import PropTypes from 'prop-types';
import axios from 'axios';

// material-ui
import Skeleton from '@mui/material/Skeleton';
import { Box, Card, CardContent, Grid, Typography, TextField, FormControl, Button, Input } from '@mui/material';
import Backdrop from '@mui/material/Backdrop';
import CircularProgress from '@mui/material/CircularProgress';

import Paper from '@mui/material/Paper';
import Table from '@mui/material/Table';
import TableBody from '@mui/material/TableBody';
import TableCell from '@mui/material/TableCell';
import TableContainer from '@mui/material/TableContainer';
import TableHead from '@mui/material/TableHead';
import TableFooter from '@mui/material/TableFooter';
import TablePagination from '@mui/material/TablePagination';
import TableRow from '@mui/material/TableRow';
import Select from '@mui/material/Select';
import MenuItem from '@mui/material/MenuItem';
import InputLabel from '@mui/material/InputLabel';

// project imports
import SubCard from 'ui-component/cards/SubCard';
import MainCard from 'ui-component/cards/MainCard';
import SecondaryAction from 'ui-component/cards/CardSecondaryAction';
import { gridSpacing } from 'store/constant';

// third party
import * as Yup from 'yup';
import { Formik } from 'formik';

const Stocktaking = ({ ...others }) => {
    const navigate = useNavigate();
    const [stocks, setStocks] = useState([]);
    const [products, setProducts] = useState([]);
    const [productselected, setProductselected] = useState([]);
    const [selectedproduct, setSelectedproduct] = useState('');
    const [specificproductid, setSpecificproductid] = useState('');
    const [brandnamess, setBrandname] = useState('');
    const [brandtypess, setBrandtype] = useState('');
    const [pricertl, setPricertl] = useState(0);
    const [pricews, setPricews] = useState(0);
    const [open, setOpen] = useState(true);

    const specificproductidref = useRef();
    specificproductidref.current = specificproductid;

    const [waitmsg, setWaitmsg] = useState('Please Wait');

    const config = {
        headers: {
            'Content-Type': 'application/json',
            Authorization: `Bearer ${localStorage.getItem('authToken')}`
        }
    };

    const handleClose = () => {
        setWaitmsg('Try going back⏱ or check your network🔌');
    };

    // 👇👇 FINDALL

    useEffect(() => {
        const getData = async () => {
            try {
                setWaitmsg('Starting products fetch');
                await axios.get(`https://dry-stream-10813.herokuapp.com/api/private/getallproducts`, config).then((response) => {
                    setProducts(response.data.data);
                });
                setWaitmsg('Done fetching products');

                setWaitmsg('Starting stocks fetch');
                await axios.get(`https://dry-stream-10813.herokuapp.com/api/private/getallstocks`, config).then((sresponse) => {
                    setStocks(sresponse.data.data);
                });
                setWaitmsg('Done fetching stocks');

                setOpen(false);
            } catch (error) {
                setWaitmsg(error.message);
            }
        };

        getData();
    }, []);

    console.log(stocks);

    const reFetch = async () => {
        setWaitmsg('Starting stocks fetch');
        await axios.get(`https://dry-stream-10813.herokuapp.com/api/private/getallstocks`, config).then((sresponse) => {
            setStocks(sresponse.data.data);
        });
        setWaitmsg('Done fetching stocks');
    };

    function createData(name, calories, fat, carbs, protein) {
        return { name, calories, fat, carbs, protein };
    }

    const rows = [
        createData('Safari', 159, 6.0, 24, 4.0),
        createData('Supermatch', 237, 9.0, 37, 4.3),
        createData('Superman', 262, 16.0, 24, 6.0),
        createData('Roster', 305, 3.7, 67, 4.3),
        createData('IMSI', 356, 16.0, 49, 3.9)
    ];

    const handleProductChange = async (event) => {
        setSpecificproductid(event.target.value);
        const theproductid = event.target.value;

        try {
            if (specificproductid !== '') {
                setOpen(true);
                setWaitmsg('Starting specific product fetch');
                const { data } = await axios.get(`https://dry-stream-10813.herokuapp.com/api/private/getproductwid/${theproductid}`, config);
                setWaitmsg('Setting specific product details');
                setSelectedproduct(data.data);
                console.log(data.data);
                setWaitmsg('Setting specific product prices');
                setPricertl(data.data.brandtypepricertl);
                setPricews(data.data.brandtypepricews);
                setWaitmsg('Setting specific product names');
                setBrandname(data.data.brandname);
                setBrandtype(data.data.brandtype);
                setWaitmsg('Done');
                setOpen(false);
            }
        } catch (error) {
            console.log(error);
        }
    };

    function pleaseWait(message) {
        return (
            <div>
                <Backdrop sx={{ color: '#fff', zIndex: (theme) => theme.zIndex.drawer + 1 }} open={open} onClick={handleClose}>
                    <CircularProgress color="inherit" />
                    <Typography>{waitmsg}</Typography>
                </Backdrop>
            </div>
        );
    }

    return (
        <MainCard title="Color Palette" secondary={<SecondaryAction link="/" />}>
            <Backdrop sx={{ color: '#fff', zIndex: (theme) => theme.zIndex.drawer + 1 }} open={open} onClick={handleClose}>
                <CircularProgress color="inherit" />
                <Typography sx={{ m: 4 }}>{waitmsg}</Typography>
            </Backdrop>
            <Grid container spacing={gridSpacing}>
                <Grid item xs={12}>
                    <SubCard title="Add stock" secondary={<SecondaryAction link="/" />}>
                        <Grid container spacing={gridSpacing}>
                            <TableContainer component={Paper}>
                                <Formik
                                    initialValues={{
                                        brandnametypeid: '',
                                        brandname: '',
                                        brandtype: '',
                                        openingstock: 0,
                                        addedstock: 0,
                                        closingstock: 0,
                                        soldstock: 0,
                                        ws: 0,
                                        rtl: 0,
                                        profit: 0,
                                        wssales: 0,
                                        rtlsales: 0
                                    }}
                                    validate={(values) => {
                                        const errors = {};
                                        if (
                                            !values.brandnametypeid &&
                                            !values.openingstock &&
                                            !values.addedstock &&
                                            !values.closingstock &&
                                            !values.soldstock &&
                                            !values.ws &&
                                            !values.rtl &&
                                            !values.profit &&
                                            !values.wssales &&
                                            !values.rtlsales
                                        ) {
                                            errors.brandnametypeid = 'Required';
                                            errors.openingstock = 'Required';
                                            errors.addedstock = 'Required';
                                            errors.closingstock = 'Required';
                                            errors.soldstock = 'Required';
                                            errors.ws = 'Required';
                                            errors.rtl = 'Required';
                                            errors.profit = 'Required';
                                            errors.wssales = 'Required';
                                            errors.rtlsales = 'Required';
                                        }
                                        return errors;
                                    }}
                                    onSubmit={async (values, { setSubmitting, resetForm }) => {
                                        const config = {
                                            headers: {
                                                'Content-Type': 'application/json',
                                                Authorization: `Bearer ${localStorage.getItem('authToken')}`
                                            }
                                        };

                                        const brandnametypeid = specificproductid;
                                        const brandname = brandnamess;
                                        const brandtype = brandtypess;
                                        const openingstock = values.openingstock;
                                        const addedstock = values.addedstock;
                                        const closingstock = values.closingstock;
                                        const soldstock = values.openingstock + values.addedstock - values.closingstock;
                                        const ws = pricews * values.wssales;
                                        const rtl = pricertl * values.rtlsales;
                                        const wssales = values.wssales;
                                        const rtlsales = values.rtlsales;
                                        const profit = pricews * values.wssales + pricertl * values.rtlsales;

                                        try {
                                            alert(
                                                `${brandnametypeid},${brandname},${brandtype},${openingstock},${addedstock},${closingstock},${soldstock},${ws},${rtl},${wssales},${rtlsales},${profit}`
                                            );
                                            const { data } = await axios.post(
                                                'https://dry-stream-10813.herokuapp.com/api/private/addstock',
                                                {
                                                    brandnametypeid,
                                                    brandname,
                                                    brandtype,
                                                    openingstock,
                                                    addedstock,
                                                    closingstock,
                                                    soldstock,
                                                    ws,
                                                    rtl,
                                                    wssales,
                                                    rtlsales,
                                                    profit
                                                },
                                                config
                                            );

                                            if (data === '') {
                                                setSubmitting(true);
                                                setOpen(true);
                                            } else {
                                                setSubmitting(false);
                                                setOpen(false);
                                            }
                                            resetForm({});
                                            reFetch();
                                        } catch (err) {
                                            console.error(err);
                                        }
                                    }}
                                >
                                    {({
                                        values,
                                        errors,
                                        touched,
                                        handleChange,
                                        handleBlur,
                                        handleSubmit,
                                        isSubmitting
                                        /* and other goodies */
                                    }) => (
                                        <form onSubmit={handleSubmit}>
                                            {isSubmitting ? (
                                                <Card>
                                                    <CardContent>
                                                        <Grid container direction="column">
                                                            <Grid item>
                                                                <Grid container justifyContent="space-between">
                                                                    <Grid item>
                                                                        <Skeleton variant="rectangular" width={88} height={44} />
                                                                    </Grid>
                                                                    <Grid item>
                                                                        <Skeleton variant="rectangular" width={88} height={44} />
                                                                    </Grid>
                                                                    <Grid item>
                                                                        <Skeleton variant="rectangular" width={88} height={44} />
                                                                    </Grid>
                                                                    <Grid item>
                                                                        <Skeleton variant="rectangular" width={88} height={44} />
                                                                    </Grid>
                                                                    <Grid item>
                                                                        <Skeleton variant="rectangular" width={88} height={44} />
                                                                    </Grid>
                                                                    <Grid item>
                                                                        <Skeleton variant="rectangular" width={88} height={44} />
                                                                    </Grid>
                                                                    <Grid item>
                                                                        <Skeleton variant="rectangular" width={88} height={44} />
                                                                    </Grid>
                                                                    <Grid item>
                                                                        <Skeleton variant="rectangular" width={88} height={44} />
                                                                    </Grid>
                                                                </Grid>
                                                            </Grid>
                                                            <Grid item>
                                                                <Skeleton variant="rectangular" sx={{ my: 2 }} height={40} />
                                                            </Grid>
                                                            <Grid item>
                                                                <Skeleton variant="rectangular" height={30} />
                                                            </Grid>
                                                        </Grid>
                                                    </CardContent>
                                                </Card>
                                            ) : (
                                                <TableContainer component={Paper}>
                                                    <Table sx={{ minWidth: 650 }} aria-label="simple table">
                                                        <TableHead>
                                                            <TableRow>
                                                                <TableCell align="center">Select Product</TableCell>
                                                                <TableCell align="center">Opening</TableCell>
                                                                <TableCell align="center">Added</TableCell>
                                                                <TableCell align="center">Closing</TableCell>
                                                                <TableCell align="center">Sold</TableCell>
                                                                <TableCell align="center">WS P</TableCell>
                                                                <TableCell align="center">RS P</TableCell>
                                                                <TableCell align="center">Profit</TableCell>
                                                            </TableRow>
                                                        </TableHead>
                                                        <TableBody>
                                                            <TableRow sx={{ '&:last-child td, &:last-child th': { border: 0 } }}>
                                                                <TableCell component="th" scope="row">
                                                                    <Select
                                                                        name="brandnametypeid"
                                                                        labelId="demo-simple-select-standard-label"
                                                                        id="demo-simple-select-standard"
                                                                        value={specificproductid}
                                                                        onChange={handleProductChange}
                                                                        label="Age"
                                                                        tabindex="0"
                                                                    >
                                                                        <MenuItem value="">
                                                                            <em>None</em>
                                                                        </MenuItem>
                                                                        {products !== undefined ? (
                                                                            products.map((option) => (
                                                                                <MenuItem key={option._id} value={option._id}>
                                                                                    {`Brand: ${option.brandname} Type: ${option.brandtype}`}
                                                                                </MenuItem>
                                                                            ))
                                                                        ) : (
                                                                            <MenuItem value="">
                                                                                <em>You do not have products</em>
                                                                            </MenuItem>
                                                                        )}
                                                                    </Select>
                                                                </TableCell>
                                                                <TableCell component="th" scope="row">
                                                                    <Input
                                                                        name="openingstock"
                                                                        helpertext="The brand name of the prodduct"
                                                                        id="demo-helper-text-aligned"
                                                                        label="Brand Name"
                                                                        type="number"
                                                                        onChange={handleChange}
                                                                        onBlur={handleBlur}
                                                                        value={values.openingstock}
                                                                        tabindex="-1"
                                                                    />
                                                                </TableCell>
                                                                <TableCell align="right">
                                                                    <Input
                                                                        name="addedstock"
                                                                        helpertext="The brand name of the prodduct"
                                                                        id="demo-helper-text-aligned"
                                                                        label="Brand Name"
                                                                        type="number"
                                                                        onChange={handleChange}
                                                                        onBlur={handleBlur}
                                                                        value={values.addedstock}
                                                                        tabindex="-2"
                                                                    />
                                                                </TableCell>
                                                                <TableCell align="right">
                                                                    <Input
                                                                        name="closingstock"
                                                                        helpertext="The brand name of the prodduct"
                                                                        id="demo-helper-text-aligned"
                                                                        label="Brand Name"
                                                                        type="number"
                                                                        onChange={handleChange}
                                                                        onBlur={handleBlur}
                                                                        value={values.closingstock}
                                                                        tabindex="-3"
                                                                    />
                                                                </TableCell>
                                                                <TableCell align="right">
                                                                    <Input
                                                                        name="soldstock"
                                                                        helpertext="The brand name of the prodduct"
                                                                        id="demo-helper-text-aligned"
                                                                        label="Brand Name"
                                                                        type="number"
                                                                        onChange={handleChange}
                                                                        onBlur={handleBlur}
                                                                        value={
                                                                            values.openingstock + values.addedstock - values.closingstock
                                                                        }
                                                                        disabled
                                                                    />
                                                                </TableCell>
                                                                <TableCell align="right">
                                                                    <Input
                                                                        name="ws"
                                                                        helpertext="The brand name of the product"
                                                                        id="demo-helper-text-aligned"
                                                                        label={`WS P: ${pricews}`}
                                                                        type="number"
                                                                        onChange={handleChange}
                                                                        onBlur={handleBlur}
                                                                        value={pricews * values.wssales}
                                                                        disabled
                                                                    />
                                                                </TableCell>
                                                                <TableCell align="right">
                                                                    <Input
                                                                        name="rtl"
                                                                        helpertext="The brand name of the product"
                                                                        id="demo-helper-text-aligned"
                                                                        label="Brand Name"
                                                                        type="number"
                                                                        onChange={handleChange}
                                                                        onBlur={handleBlur}
                                                                        value={pricertl * values.rtlsales}
                                                                        disabled
                                                                    />
                                                                </TableCell>
                                                                <TableCell align="right">
                                                                    <Input
                                                                        name="profit"
                                                                        helpertext="The brand name of the product"
                                                                        id="demo-helper-text-aligned"
                                                                        label="Brand Name"
                                                                        type="number"
                                                                        onChange={handleChange}
                                                                        onBlur={handleBlur}
                                                                        value={pricews * values.wssales + pricertl * values.rtlsales}
                                                                        disabled
                                                                    />
                                                                </TableCell>
                                                            </TableRow>
                                                            <TableRow>
                                                                <TableCell />
                                                                <TableCell />
                                                                <TableCell />
                                                                <TableCell />
                                                                <TableCell />
                                                                <TableCell align="right">
                                                                    <Input
                                                                        name="wssales"
                                                                        helpertext="The brand name of the product"
                                                                        id="demo-helper-text-aligned"
                                                                        label="Brand Name"
                                                                        type="number"
                                                                        onChange={handleChange}
                                                                        onBlur={handleBlur}
                                                                        value={values.wssales}
                                                                        tabindex="-4"
                                                                    />
                                                                </TableCell>
                                                                <TableCell align="right">
                                                                    <Input
                                                                        name="rtlsales"
                                                                        helpertext="The brand name of the product"
                                                                        id="demo-helper-text-aligned"
                                                                        label="Brand Name"
                                                                        type="number"
                                                                        onChange={handleChange}
                                                                        onBlur={handleBlur}
                                                                        value={values.rtlsales}
                                                                        tabindex="-5"
                                                                    />
                                                                </TableCell>
                                                                <TableCell align="right">
                                                                    <Button type="submit" disabled={isSubmitting}>
                                                                        Submit
                                                                    </Button>
                                                                </TableCell>
                                                            </TableRow>
                                                        </TableBody>
                                                        <TableFooter>
                                                            <TableRow>
                                                                <TableCell />
                                                                <TableCell />
                                                                <TableCell />
                                                                <TableCell />
                                                                <TableCell />
                                                                <TableCell align="center">W/Sales</TableCell>
                                                                <TableCell align="center">R/Sales</TableCell>
                                                                <TableCell />
                                                            </TableRow>
                                                        </TableFooter>
                                                    </Table>
                                                </TableContainer>
                                            )}
                                        </form>
                                    )}
                                </Formik>
                            </TableContainer>
                        </Grid>
                    </SubCard>
                </Grid>
                <Grid item xs={12}>
                    <SubCard title="Stocks">
                        <Grid container spacing={gridSpacing}>
                            <TableContainer component={Paper}>
                                <Table sx={{ minWidth: 650 }} aria-label="simple table">
                                    <TableHead>
                                        <TableRow>
                                            <TableCell>Brand</TableCell>
                                            <TableCell align="right">Variant</TableCell>
                                            <TableCell align="right">Opening</TableCell>
                                            <TableCell align="right">Added</TableCell>
                                            <TableCell align="right">Closing</TableCell>
                                            <TableCell align="right">Sold</TableCell>
                                            <TableCell align="right">WS_S</TableCell>
                                            <TableCell align="right">RT_S</TableCell>
                                            <TableCell align="right">WS_P</TableCell>
                                            <TableCell align="right">RT_P</TableCell>
                                            <TableCell align="right">Total P</TableCell>
                                        </TableRow>
                                    </TableHead>
                                    <TableBody>
                                        {stocks !== undefined ? (
                                            stocks.map((val, key) => (
                                                <TableRow key={val._id} sx={{ '&:last-child td, &:last-child th': { border: 0 } }}>
                                                    <TableCell component="th" scope="val">
                                                        {val.brandname}
                                                    </TableCell>
                                                    <TableCell align="right">{val.brandtype}</TableCell>
                                                    <TableCell align="right">{val.openingstock}</TableCell>
                                                    <TableCell align="right">{val.addedstock}</TableCell>
                                                    <TableCell align="right">{val.closingstock}</TableCell>
                                                    <TableCell align="right">{val.soldstock}</TableCell>
                                                    <TableCell align="right">{val.wssales}</TableCell>
                                                    <TableCell align="right">{val.rtlsales}</TableCell>
                                                    <TableCell align="right">{val.ws}</TableCell>
                                                    <TableCell align="right">{val.rtl}</TableCell>
                                                    <TableCell align="right">{val.profit}</TableCell>
                                                </TableRow>
                                            ))
                                        ) : (
                                            <TableRow>No Stocks</TableRow>
                                        )}
                                    </TableBody>
                                    <TableFooter>
                                        <TableRow>
                                            <TableCell>Brand</TableCell>
                                            <TableCell align="right">Variant</TableCell>
                                            <TableCell align="right">Opening</TableCell>
                                            <TableCell align="right">Added</TableCell>
                                            <TableCell align="right">Closing</TableCell>
                                            <TableCell align="right">Sold</TableCell>
                                            <TableCell align="right">WS_S</TableCell>
                                            <TableCell align="right">RT_S</TableCell>
                                            <TableCell align="right">WS_P</TableCell>
                                            <TableCell align="right">RT_P</TableCell>
                                            <TableCell align="right">Total P</TableCell>
                                        </TableRow>
                                    </TableFooter>
                                </Table>
                            </TableContainer>
                        </Grid>
                    </SubCard>
                </Grid>
            </Grid>
        </MainCard>
    );
};

export default Stocktaking;
